import { z } from 'zod';

import {
  SemanticTriggerFiltersInterface,
  SemanticTriggerFiltersSchema,
} from './semantic-trigger-filters.interface';

export const SemanticTriggerSchema = z.object({
  name: z.string().min(1).optional(),
  description: z.string().min(1).optional(),
  eventType: z.string().min(1),
  dataset: z.string().min(1),
  dimensions: z.array(z.string().min(1)).min(1),
  limit: z.number().min(1).optional(),
  filters: SemanticTriggerFiltersSchema.optional(),
  cron: z.string().nullable().optional(),
  tz: z.string().nullable().optional(),
  incrementalField: z.string().optional(),
  ungrouped: z.boolean().optional(),
});

export interface SemanticTriggerInterface {
  name?: string;
  description?: string;
  eventType: string;
  dataset: string;
  dimensions: Array<string>;
  measures?: Array<string>;
  segments?: Array<string>;
  limit?: number;
  filters?: SemanticTriggerFiltersInterface | false;
  cron?: string;
  tz?: string;
  incrementalField?: string;
  ungrouped?: boolean;
}
